                        </div>
                    </div>
                    <div class="col-12 conod LR" id="msgbox" @click="remove()" style="display:none;">   
                        <div class="ssmg fadein" id="msg"  ><div class="tf-14 pb-2"></div></div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import axios from "axios";
export default {
    name: "MineView",
    data() {
        return {
            changename:null,
            changetext:null,
            changetext1:null,
            change:null,
            userdetails: {
                id: null,
                nickname: null,
                username: null,
                balance: null,
                usercode: null,
                commision: "0",
                intrest: "0",
                notice: "Welcome",

            },
            user: [],
            dailysign: []
        }

    },
    beforeCreate: function () {
        if (localStorage.getItem('username') == null) {
            this.$router.push({ name: 'login' });
        }

    },
    created: function () {
        this.getUserdetails();


    },


    methods: {
        pop(message) {
            document.getElementById("msg").innerHTML = message;
            document.getElementById("msgbox").style.display = "";
            setTimeout(this.getUserdetails(),1500);
        },
        changebox(name){
            document.getElementById("clink").style.display = "";
            document.getElementById("change").style.display = "";
            if(name=='nickname'){
                this.changename='nickname';
                this.changetext='Change Nick Name';
                this.changetext1='Enter Nick Name';
            }else if(name=='password'){  
                this.changename='password';
                this.changetext='Set New Password';
                this.changetext1='New Password';
            }
        },
        cancel(){
            document.getElementById("clink").style.display = "none";
            this.change=null;
        },
        confirmchange(){
            
            if(this.changename=='nickname'){
                if(this.change==null){
                    this.pop("Enter Your Name");
                    this.cancel();
                }else{
                    axios.get('https://jhargames.com/9987/src/api/bet.php?action=changenickname&user=' + this.userdetails.username +'&nickname='+this.change);
                  
                    this.pop("Successfully");
                    this.cancel();
                    
                }
            }else if(this.changename=='password'){
                if(this.change==null){
                    this.pop("Please Enter Password");
                    this.cancel();
                }else{
                    axios.get('https://jhargames.com/9987/src/api/bet.php?action=changepassword&user=' + this.userdetails.username +'&password='+this.change);
                    
                    this.pop("Successfully");
                    this.cancel();
                }
            }
        },
        remove(){
            document.getElementById("msgbox").style.display = "none";
            this.getUserdetails();
        },
        logout() {
            localStorage.removeItem('username')
            this.$router.push("/login");
        },
  
       
      
        getUserdetails() {

            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://jhargames.com/9987/src/api/me.php?action=getuserinfo&user=' + this.userdetails.username).then((res) => {
                this.Users = res.data.user_Data;
                this.userdetails.id = this.Users[0].id;
                this.userdetails.balance = this.Users[0].balance;
                this.userdetails.usercode = this.Users[0].usercode;
                this.userdetails.nickname = this.Users[0].nickname;
            }).catch((err) => {
                console.log(err);
            })



        }
    }

}
</script>