            username: null,
            balance: 0.00,
        }

    },
    mounted: function () {
        this.getUserdetails()
    },
    methods: {
        amount(val) {
            document.getElementById("recharge").value = val;
        }
        ,
        getUserdetails() {

            this.username = localStorage.getItem('username');
            axios.get('https://jhargames.com/9987/src/api/me.php?action=getuserinfo&user=' + this.username).then((res) => {
                this.Users = res.data.user_Data;

                this.balance = this.Users[0].balance;

                console.log(this.Users);
            }).catch((err) => {
                console.log(err);
            })
        },
        proceed() {


            if (document.getElementById("recharge").value > 299) {
                var link = "https://jhargames.com/payment/payment.php?user=" + this.username + "&am="
                var amount = document.getElementById("recharge").value;
                var payurl = link.concat(amount);
                window.open(payurl, "_blank");
            }
            else {
                alert("Minimum recharge 300")
                var x = document.getElementById("snackbar");
                x.style.display = "";
                setTimeout(function e() { x.style.display = "none"; }, 1000);

            }
        }
