<script>
import axios from "axios";
export default {
    name: "WinView",
    props: ['rowsrec'],
    data() {
        return {
            isActive: false,
            userdetails: {
                username: null,
                balance: null,
                period: null,

            },
            betbox: {
                title: null,
                target: null,
                boxcolor: null,
                timeout: false,
                multiply: 1,
                factor: 10,
                amount: 10,
                page: 1,
                page1: 1,
                total: 1000,
                total1: null,

            },
            resultrec: '',
            info: '',
            betrec: ''

        }

    },
    mounted: function () {
        this.countdown();
        this.refreshcountdown = setInterval(this.countdown, 1000);
 
    },
    beforeUnmount: function () {
        console.log("unmount");
        clearInterval(this.refreshcountdown);

    },
    created: function () {
        this.fechresultrec();
        // this.expand();
    },
    methods: {
        pop(message) {
            document.getElementById("message").innerHTML = message;
            document.getElementById("clink").style.display = "";

        }, cpop() {
            document.getElementById("clink").style.display = "none";

        },
        fechresultrec() {
            axios.get('https://jhargames.com/9987/src/api/bet.php?action=result&server=FastParity').then((res) => {
                this.resultrec = res.data;
                console.log(this.resultrec);
            }).catch((err) => {
                console.log(err);
            })
            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://jhargames.com/9987/src/api/bet.php?action=betrec&user=' + this.userdetails.username + '&server=FastParity').then((res) => {
                this.betrec = res.data;
                console.log(this.betrec);
            }).catch((err) => {
                console.log(err);
            })

            axios.get('https://jhargames.com/9987/src/api/bet.php?action=info&user=' + this.userdetails.username+'&per=FastParity').then((res) => {
                this.info = res.data;
                this.betbox.total = this.info[1].total;
                this.betbox.total1 = this.info[3].total1;
                this.userdetails.balance = this.info[0].balance;
                this.userdetails.period = this.info[2].period;
                console.log(this.info);
            }).catch((err) => {
                console.log(err);
            })
        },
        betamount(x, y, z, a) {
            var amount;
            var f;
            if (z == "fail") {
                f = 95 / 100 * x;
                amount = "<r> -₹" + (f.toFixed(2))+"</r>";
                return amount;
            } else if (z == 'success') {
                if (a == "violet") {
                    if (y == "green" || y == "red") {
                        f = 95 / 100 * x * 1.5;
                        amount = "<g>+₹" + f.toFixed(2)+"</g>";
                        return amount;
                    } else if (y == "violet") {
                        f = 95 / 100 * x * 3;
                        amount = "<g>+₹" + f.toFixed(2)+"</g>";
                        return amount;
                    } else {
                        f = 95 / 100 * x * 9;
                        amount = "<g>+₹" + f.toFixed(2)+"</g>";
                        return amount;
                    }
                } else {
                    if (y == "green" || y == "red") {
                        f = 95 / 100 * x * 2;
                        amount = "<g>+₹" + f.toFixed(2)+"</g>";
                        return amount;
                    } else if (y == "violet") {
                        f = 95 / 100 * x * 1.5;
                        amount = "<g>+₹" + f.toFixed(2)+"</g>";
                        return amount;
                    } else {
                        f = 95 / 100 * x * 9;
                        amount = "<g>+₹" + f.toFixed(2)+"</g>";
                        return amount;
                    }
                }

            } else {
                return "<span class='icon wait'></span>";
            }
        },
        readrule() {
            document.getElementById("xrule").style.display = "";
        },
        closerule() {
            document.getElementById("xrule").style.display = "none";
        },
        join(label) {
            console.log(label);
            if (this.betbox.timeout) {
                if (label == "Green") {
                    this.betbox.target = "green";
                    this.betbox.boxcolor = "green";
                } else if (label == "Red") {
                    this.betbox.target = "red";
                    this.betbox.boxcolor = "red";
                } else if (label == "Violet") {
                    this.betbox.target = "violet";
                    this.betbox.boxcolor = "violet";
                } else {
                    this.betbox.target = label;
                    this.betbox.boxcolor = "Violet";
                }
                this.betbox.title = label;
                document.getElementById("nfrm").style.display = "block";
                document.getElementById("blackscreen").style.display = "block";
                this.fechresultrec();
            }

        },
        closescreen() {
            document.getElementById("nfrm").style.display = "none";
            document.getElementById("blackscreen").style.display = "none";
        },
        betclass(name) {
            return name + ' ' + this.betbox.boxcolor;
        },
      
        closebetbox() {
            document.getElementById("betbox").style.display = "none";
            var listItems = document.getElementById('factorlist').childNodes;
            // Remove 'active' tag for all list items
            for (let i = 0; i < listItems.length; i++) {
                listItems[i].classList.remove('active');
            }
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
            document.getElementById("10").className = "active";
            this.betbox.multiply = 1;
            this.betbox.factor = 10;
            this.betbox.amount = 10;
        },
        selectmultiply(multi) {
            var listItems = document.getElementById('factorlist').childNodes;
            // Remove 'active' tag for all list items
            for (let i = 0; i < listItems.length; i++) {
                listItems[i].classList.remove('selected');
            }
            this.betbox.factor = multi;
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
            document.getElementById(multi).className = "cont-amt selected " + this.betbox.target;
        },
        plus(value) {
            this.betbox.multiply = this.betbox.multiply + value;
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
        },
        minus(value) {
            if (this.betbox.multiply > value) {
                this.betbox.multiply = this.betbox.multiply - value;
                this.betbox.amount = this.betbox.factor * this.betbox.multiply
            }

        },
        myorder(){
            document.getElementById("every").classList.remove("active");
            document.getElementById("my").classList.add("active");
            document.getElementById("myorder").style.display="";
            document.getElementById("everyoneorder").style.display="none";
        },
        everyoneorder(){
            document.getElementById("my").classList.remove("active");
            document.getElementById("every").classList.add("active");
            document.getElementById("myorder").style.display="none";
            document.getElementById("everyoneorder").style.display="";
        },
        countdown() {
            var countDownDate = Date.parse(new Date) / 1e3;
            //var now = new Date().getTime();
            var distance = 30 - countDownDate % 30;
            //alert(distance);
            var i = distance / 60,
                n = distance % 60;
            //o = n / 10,
            // s = n % 10;
            var minutes = Math.floor(i);
            var seconds = ('0' + Math.floor(n)).slice(-2);
            var sec1 = (seconds % 100 - seconds % 10) / 10;
            var sec2 = seconds % 10;
            document.getElementById("demo").innerHTML = "<span class='cdspn'>0</span><span class='cdspn' id='fsm'> " + Math.floor(minutes) + "</span>:<span class='cdspn' id='fs0'>" + sec1 + "</span><span class='cdspn' id='fs1'>" + sec2 + "</span>";
            if (7 < distance) {
                document.getElementById("jcr").classList.remove("disabled");
                document.getElementById("jcg").classList.remove("disabled");
                document.getElementById("jcv").classList.remove("disabled");
                document.getElementById("jn0").classList.remove("disabled");
                document.getElementById("jn1").classList.remove("disabled");
                document.getElementById("jn3").classList.remove("disabled");
                document.getElementById("jn4").classList.remove("disabled");
                document.getElementById("jn5").classList.remove("disabled");
                document.getElementById("jn6").classList.remove("disabled");
                document.getElementById("jn7").classList.remove("disabled");
                document.getElementById("jn8").classList.remove("disabled");
                document.getElementById("jn9").classList.remove("disabled");
                document.getElementById("jn2").classList.remove("disabled");
                this.betbox.timeout = true;
            }
            if (7 > distance) {
                document.getElementById("jcr").classList.add("disabled");
                document.getElementById("jcg").classList.add("disabled");
                document.getElementById("jcv").classList.add("disabled");
                document.getElementById("jn0").classList.add("disabled");
                document.getElementById("jn1").classList.add("disabled");
                document.getElementById("jn2").classList.add("disabled");
                document.getElementById("jn3").classList.add("disabled");
                document.getElementById("jn4").classList.add("disabled");
                document.getElementById("jn5").classList.add("disabled");
                document.getElementById("jn6").classList.add("disabled");
                document.getElementById("jn7").classList.add("disabled");
                document.getElementById("jn8").classList.add("disabled");
                document.getElementById("jn9").classList.add("disabled");
                document.getElementById("nfrm").style.display = "none";
                document.getElementById("everyone").stop();
                document.getElementById("blackscreen").style.display = "none";
                this.betbox.timeout = false;
            }
            if (30 == distance) {
                this.fechresultrec();
            }
           
            if (28== distance) {
                this.fechresultrec();
            }
            if (29 == distance) {
                this.fechresultrec();
                document.getElementById("everyone").start();
                document.getElementById("everyone").start();
            }
            

        },
        toggle(idx) {
            if (document.getElementById(idx).style.display == "") {
                document.getElementById(idx).style.display = "none";
            } else {
                document.getElementById(idx).style.display = "";
            }
        },
        confirmbet() {
            document.getElementById("nfrm").style.display = "none";
            document.getElementById("blackscreen").style.display = "none";
            if (this.userdetails.balance >= this.betbox.amount) {
                var data = new FormData();
                data.append("username", this.userdetails.username);
                data.append("period", this.userdetails.period);
                data.append("amount", this.betbox.amount);
                data.append("ans", this.betbox.target);
                axios
                    .post(
                        "https://jhargames.com/9987/src/api/bet.php?action=bet&server=FastParity",
                        data
                    )
                    .then(res => {
                        console.log(res.data);
                        this.closebetbox();
                        this.fechresultrec();
                    })
                    .catch(err => {
                        console.log("Error", err);
                    });
                    this.fechresultrec();
            } else {
                this.pop("Low Balance!");
            }

        },
        load() {
            this.fechresultrec();
        },
        getcolor(stat) {
            if (stat == "red") {
                return 'RS';
            } else if (stat == "green") {
                return 'GS';
            }  else if (stat == "violet") {
                return 'VS';
            } else {
                return 'NS';
            }
        },
    getele(ele){
        if(ele%2==1){
            if(ele==5){
                return "<div class='GS'><div class='vil'></div><div class='tpr'>"+ele+"</div></div>"
            }else{
                return "<div class='GS'><div class=''></div><div class='tpr'>"+ele+"</div></div>" 
            }
            
        }else if(ele%2==0){
            if(ele==0){
                return "<div class='RS'><div class='vil'></div><div class='tpr'>"+ele+"</div></div>"
            }else{
                return "<div class='RS'><div class=''></div><div class='tpr'>"+ele+"</div></div>" 
            }
        }else{
            return "<div class='' style='margin-left: 25%;'><span class='icon wait'></span></div>"
        }
    },
        short(stat){
            if (stat == "red") {
                return 'R';
            } else if (stat == "green") {
                return 'G';
            }else if (stat == "violet") {
                return 'V';
            }  else {
                return stat;
            } 
        }
     
    

    }
}

</script>