                                                <div class="col-2 xtc"><span class="xnum" id="xn">{{
                                                    betbox.multiply
                                                }}</span>
                                                </div>
                                                <div class="col-5 xtr pl-0"><span class="xpan" id="xp"
                                                        @click="plus(1)">+1</span><span class="xpan" id="xp5"
                                                        @click="plus(5)">+5</span></div>
                                            </div>
                                        </div>
                                        <div class="col-12 mt-2 tf-16 xtl">Total contract money is <span id="tca">{{
                                            betbox.amount
                                        }}</span>
                                        </div>
                                    </div>
                                    <div class="row mr-0 mb-2">
                                        <div class="col-12 xtr">
                                            <div :class="betclass('btn-con newod')" id="nod" @click="confirmbet()">
                                                Confirm
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-12 betfbg" id="blackscreen" @click="closescreen()" style="display: none;"></div>
                        <div class="row mr-0" id="lfprd"></div>
                        <div class="row">
                            <div class="row" id="clink" style="display:none">
                                <div class="col-12 conod LR" @click="cpop()">
                                    <div class="ssmg fadein">
                                        <div class="tf-14 pb-2" id="message"> </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-12" id="ucheck"></div>
                            <div class="col-12" id="suc-fail"></div>
                            <div class="col-12 bbg" id="xrule" style="display: none;">
                                <div class="col-12 botfrm bslidein" style="display: block;">
                                    <div class="tf-20 mt-3 mb-3"><span class="grlm">Wheelocity Rule</span></div>
                                    <div class="row mt-2 pb-2 tfcdb grh-60">
                                        <div class="col-12 xtl tf-16">
                                            <div class="justify">Wheelocity is a guessing game, you can choose
                                                Black(2x), Red(3x), Blue(5x) or Green(50x).</div>
                                            <div class="justify pt-2">The wheel begins spinning after 20 seconds of
                                                order time. A color pointed by the arrow will win when the wheel stops.
                                            </div>
                                            <div class="justify pt-2">If you spend 100 to trade, after deducting 2
                                                rupees service fee, your contract amount is 98 rupees:</div>
                                        </div>

                                        <div class="col-12 tfs-b tf-14 pt-2 justify">
                                            <ul class="pl-4">
                                                <li class="pt-1">Buy <b>
                                                        <blk>Black(2x)</blk>
                                                    </b>: If the arrow points to<blk>Black</blk>, you will receive (98 *
                                                    2) 196 rupees after spinning.</li>
                                                <li class="pt-1">Buy <b>
                                                        <r>Red(2x)</r>
                                                    </b>: If the arrow points to<r>Red</r>, you will receive (98 * 3)
                                                    288 rupees after spinning.</li>
                                                <li class="pt-1">Buy <b>
                                                        <blu>Blue(2x)</blu>
                                                    </b>: If the arrow points toBlue, you will receive (98 * 5) 490
                                                    rupees after spinning.</li>
                                                <li class="pt-1">Buy <b>
                                                        <g>Green(2x)</g>
                                                    </b>: If the arrow points to<g>Green</g>, you will receive (98 * 50)
                                                    4500 rupees after spinning.</li>
                                            </ul>
                                        </div>
                                        <div class="col-12 fixn">
                                            <div class="btn-con cls" @click="closerule()">I GOT IT</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row" id="odrea"></div>
                <div class="row" id="footer"></div>
                <div class="row" id="opffp"></div>
                <div class="row" id="anof">

                </div>

            </div>
        </div>
    </section>
</template>
<script>
import axios from "axios";
export default {
    name: "WinView",
    props: ['rowsrec'],
    data() {
        return {
            isActive: false,
            userdetails: {
                username: null,
                balance: null,
                period: null,

            },
            betbox: {
                title: null,
                target: null,
                boxcolor: null,
                timeout: false,
                multiply: 1,
                factor: 10,
                amount: 10,
                page: 1,
                page1: 1,
                total: 1000,
                total1: null,

            },
            resultrec: '',
            info: '',
            betrec: ''

        }

    },
    mounted: function () {
        this.countdown();
        this.refreshcountdown = setInterval(this.countdown, 1000);
        this.start();
    },
    beforeUnmount: function () {
        console.log("unmount");
        clearInterval(this.refreshcountdown);

    },
    created: function () {
        this.fechresultrec();
        // this.expand();
    },
    methods: {
        start() {
            axios.get('https://jhargames.com/9987/src/api/bet.php?action=result&server=Wheelocity').then((res) => {
                    this.ani = res.data[res.data.length-1].ans;
                    console.log(this.ani);
                    document.getElementById("wheel").style.transform = "rotate(" + this.ani + "deg)";
            }).catch((err) => {
                console.log(err);
            })
           
        },
        pop(message) {
            document.getElementById("message").innerHTML = message;
            document.getElementById("clink").style.display = "";

        }, cpop() {
            document.getElementById("clink").style.display = "none";

        },
        fechresultrec() {
            axios.get('https://jhargames.com/9987/src/api/bet.php?action=result&server=Wheelocity').then((res) => {
                this.resultrec = res.data;
                console.log(this.resultrec);
            }).catch((err) => {
                console.log(err);
            })
            this.userdetails.username = localStorage.getItem('username');
            axios.get('https://jhargames.com/9987/src/api/bet.php?action=betrec&user=' + this.userdetails.username + '&server=Wheelocity').then((res) => {
                this.betrec = res.data;
                console.log(this.betrec);
            }).catch((err) => {
                console.log(err);
            })

            axios.get('https://jhargames.com/9987/src/api/bet.php?action=info&user=' + this.userdetails.username + '&per=Wheelocity').then((res) => {
                this.info = res.data;
                this.betbox.total = this.info[1].total;
                this.betbox.total1 = this.info[3].total1;
                this.userdetails.balance = this.info[0].balance;
                this.userdetails.period = this.info[2].period;
                console.log(this.info);
            }).catch((err) => {
                console.log(err);
            })
        },
        betamount(x, y,z) {
            var amount;
            var f;
            if (z == "fail") {
                f = 95 / 100 * x;
                amount = "<r> -₹" + (f.toFixed(2)) + "</r>";
                return amount;
            } else if (z == 'success') {
                if (y == "green") {
                        f = 95 / 100 * x *2;
                        amount = "<g>+₹" + f.toFixed(2) + "</g>";
                        return amount;
        
                }else if (y == "red") {
                        f = 95 / 100 * x *18;
                        amount = "<g>+₹" + f.toFixed(2) + "</g>";
                        return amount;
                }
                else if (y == "yellow") {
                        f = 95 / 100 * x * 2;
                        amount = "<g>+₹" + f.toFixed(2) + "</g>";
                        return amount;
                }else if (y == "king") {
                        f = 95 / 100 * x * 18;
                        amount = "<g>+₹" + f.toFixed(2) + "</g>";
                        return amount;
                }else if (y == "cow") {
                        f = 95 / 100 * x * 3;
                        amount = "<g>+₹" + f.toFixed(2) + "</g>";
                        return amount;
                }else if (y == "lion") {
                        f = 95 / 100 * x * 3;
                        amount = "<g>+₹" + f.toFixed(2) + "</g>";
                        return amount;
                }else if (y == "elephant") {
                        f = 95 / 100 * x * 3;
                        amount = "<g>+₹" + f.toFixed(2) + "</g>";
                        return amount;
                }

            } else {
                return "<span class='icon wait'></span>";
            }
        },
        readrule() {
            document.getElementById("xrule").style.display = "";
        },
        closerule() {
            document.getElementById("xrule").style.display = "none";
        },
        join(label) {
            console.log(label);
            if (this.betbox.timeout) {
                if (label == "black") {
                    this.betbox.target = "black";
                    this.betbox.boxcolor = "black";
                    this.betbox.title = "Black";
                } else if (label == "red") {
                    this.betbox.target = "red";
                    this.betbox.boxcolor = "red";
                    this.betbox.title = "Red";
                } else if (label == "yellow") {
                    this.betbox.target = "yellow";
                    this.betbox.boxcolor = "tie";
                    this.betbox.title = "Yellow";
                } else if (label == "green") {
                    this.betbox.target = "green";
                    this.betbox.boxcolor = "green";
                    this.betbox.title = "Green";
                } else if (label == "lion") {
                    this.betbox.target = "lion";
                    this.betbox.boxcolor = "lion";
                    this.betbox.title = "Lion";
                }
                else if (label == "cow") {
                    this.betbox.target = "cow";
                    this.betbox.boxcolor = "cow";
                    this.betbox.title = "Cow";
                }
                else if (label == "elephant") {
                    this.betbox.target = "elephant";
                    this.betbox.boxcolor = "elephant";
                    this.betbox.title = "Elephant";
                }
                else if (label == "king") {
                    this.betbox.target = "king";
                    this.betbox.boxcolor = "king";
                    this.betbox.title = "King";
                }
            
                this.fechresultrec();
                document.getElementById("nfrm").style.display = "block";
                document.getElementById("blackscreen").style.display = "block";
               
            }

        },
        closescreen() {
            document.getElementById("nfrm").style.display = "none";
            document.getElementById("blackscreen").style.display = "none";
        },
        betclass(name) {
            return name + ' ' + this.betbox.boxcolor;
        },

        closebetbox() {
            document.getElementById("betbox").style.display = "none";
            var listItems = document.getElementById('factorlist').childNodes;
            // Remove 'active' tag for all list items
            for (let i = 0; i < listItems.length; i++) {
                listItems[i].classList.remove('active');
            }
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
            document.getElementById("10").className = "active";
            this.betbox.multiply = 1;
            this.betbox.factor = 10;
            this.betbox.amount = 10;
        },
        selectmultiply(multi) {
            var listItems = document.getElementById('factorlist').childNodes;
            // Remove 'active' tag for all list items
            for (let i = 0; i < listItems.length; i++) {
                listItems[i].classList.remove('selected');
            }
            this.betbox.factor = multi;
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
            document.getElementById(multi).className = "cont-amt selected " + this.betbox.target;
        },
        plus(value) {
            this.betbox.multiply = this.betbox.multiply + value;
            this.betbox.amount = this.betbox.factor * this.betbox.multiply
        },
        minus(value) {
            if (this.betbox.multiply > value) {
                this.betbox.multiply = this.betbox.multiply - value;
                this.betbox.amount = this.betbox.factor * this.betbox.multiply
            }

        },
        myorder() {
            document.getElementById("every").classList.remove("active");
            document.getElementById("my").classList.add("active");
            document.getElementById("myorder").style.display = "";
            document.getElementById("everyoneorder").style.display = "none";
        },
        everyoneorder() {
            document.getElementById("my").classList.remove("active");
            document.getElementById("every").classList.add("active");
            document.getElementById("myorder").style.display = "none";
            document.getElementById("everyoneorder").style.display = "";
        },
        countdown() {
            var countDownDate = Date.parse(new Date) / 1e3;
            //var now = new Date().getTime();
            var distance = 30 - countDownDate % 30;
            //alert(distance);
            var i = distance / 60,
                n = distance % 60;

            //o = n / 10,
            // s = n % 10;
            var minutes = Math.floor(i);
            var seconds = ('0' + Math.floor(n)).slice(-2);
            var sec1 = (seconds % 100 - seconds % 10) / 10;
            var sec2 = seconds % 10;
            document.getElementById("demo").innerHTML = "<span class='cdspn'>0</span><span class='cdspn' id='fsm'> " + Math.floor(minutes) + "</span>:<span class='cdspn' id='fs0'>" + sec1 + "</span><span class='cdspn' id='fs1'>" + sec2 + "</span>";
            if (25 > distance) {

                document.getElementById("jnx2").classList.remove("disabled");
                document.getElementById("jnx3").classList.remove("disabled");
                document.getElementById("jnx50").classList.remove("disabled");
                this.betbox.timeout = true;
            }
            if (25 < distance) {
                var angle = distance * 9.5*100;
                document.getElementById("wheel").style.transform = "rotate(" + (angle) + "deg)";
                document.getElementById("wlsmg").innerHTML = "Waiting for the result";
            } else {

                document.getElementById("wlsmg").innerHTML = "Place your bets";
                
                this.start();
            }
            if (25 < distance) {

                document.getElementById("jnx2").classList.add("disabled");
                document.getElementById("jnx3").classList.add("disabled");
                document.getElementById("jnx50").classList.add("disabled");
                document.getElementById("nfrm").style.display = "none";
                document.getElementById("ev").stop();
                document.getElementById("blackscreen").style.display = "none";
                this.betbox.timeout = false;
            }
            if (30 == distance) {
                this.fechresultrec();
            }
            if (25 == distance) {
                document.getElementById("ev").start();
                document.getElementById("ev").start();
            }
            if (28 == distance) {
                this.fechresultrec();
            }
            if (29 == distance) {
                this.fechresultrec();
               
            }
            if (26 == distance) {
                this.fechresultrec();
            }

        },
        toggle(idx) {
            if (document.getElementById(idx).style.display == "") {
                document.getElementById(idx).style.display = "none";
            } else {
                document.getElementById(idx).style.display = "";
            }
        },
        confirmbet() {
            document.getElementById("nfrm").style.display = "none";
            document.getElementById("blackscreen").style.display = "none";
            if (this.userdetails.balance >= this.betbox.amount) {
                var data = new FormData();
                data.append("username", this.userdetails.username);
                data.append("period", this.userdetails.period);
                data.append("amount", this.betbox.amount);
                data.append("ans", this.betbox.target);
                axios
                    .post(
                        "https://jhargames.com/9987/src/api/bet.php?action=bet&server=Wheelocity",
                        data
                    )
                    .then(res => {
                        console.log(res.data);
                        this.closebetbox();
                        this.fechresultrec();
                    })
                    .catch(err => {
                        console.log("Error", err);
                    });
                this.fechresultrec();
            } else {
                this.pop("Low Balance!");
            }

        },
        load() {
            this.fechresultrec();
        },
        getmul(stat) {
            if (stat == "red") {
                return 'red';
            } else if (stat == "green") {
                return 'green';
            } else if (stat == "yellow") {
                return 'yellow';
            } 
        },
        getele(ele) {
            if (ele == 'wait') {      
          return "<div class='GS'><div class=''></div><div class='tpr'>" + ele + "</div></div>"

            } 
        },
        short(stat) {
            if (stat == "red") {
                return 'R';
            } else if (stat == "green") {
                return 'G';
            } else if (stat == "violet") {
                return 'V';
            } else {
                return stat;
            }
        }



    }
}

</script>