<template>
    <canvas id="c" style="background: black;height: 220px;width: 400px" ></canvas>
    <buttoN @click="bg()" >change</buttoN>
    <buttoN @click="base()" >reset</buttoN>
</template>
<script>
//import axios from "axios";
export default {
    name: "WinView",
    props: ['rowsrec'],
    data() {
        return {
       

        }

    },
    mounted: function () {
              this.base();       

    },
   
    methods: {
        base(){
            this.canwidth=900;
        this.canheight=600;
        var c = document.getElementById("c");
      this.ctx = c.getContext("2d");    
      this.vueCanvas = this.ctx;
      this.vueCanvas.width = this.canwidth;
    this.vueCanvas.height = this.canheight;
    this.ctx.clearRect(0,0,this.canheight,this.canwidth);
    this.background = new Image();
    this.background.src = "data:image/svg+xml;base64,";     
     this.ctx.drawImage(this.background,90,20,100,100); 
        },
        bg(){
            this.ctx.clearRect(0,0,this.canheight,this.canwidth);
            this.background2 = new Image();
            this.background2.src = "data:image/svg+xml;base64,";
            this.ctx.drawImage(this.background2,10,90,50,30); 
        }
    }
}

</script>